.top-actions {
  --icon-width: 24px;
  display: grid;
  grid-template-columns: 1fr var(--icon-width) var(--icon-width);
  grid-template-rows: var(--icon-width);
  column-gap: 36px;
  box-sizing: border-box;

  :first-child {
    margin-right: auto;
  }

  @include onTablet {
    --icon-width: 32px;
  }
}
