.page {
  font-family: "Inter", sans-serif;
  &__body {
    margin: 0;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transform: translateX(-100%);
    transition: transform 0.3s ;
    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 120px;
  }

  &__section-title {
    text-align: center;
    letter-spacing: -1.5px;
    font-weight: 700;
    font-size: 48px;
    line-height: 48px;
    color: $colortext;
  }
}
